/* Reset default styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Main container styles */
.main-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Content container */
.content-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
}

/* Button group styles */
.button-group {
  width: 400px;
  display: flex;
  justify-content: center;
  gap: 32px;
}

/* Button styles */
.button {
  flex: 1;
  padding: 8px 24px;
  border: none;
  border-radius: 4px;
  color: white;
  cursor: pointer;
}

.button:disabled {
  background-color: #9ca3af;
  cursor: not-allowed;
}

.button-blue {
  background-color: #3b82f6;
}

.button-red {
  background-color: #ef4444;
}

.button-green {
  background-color: #22c55e;
}

/* Slider container styles */
.slider-container {
  width: 400px;
  display: flex;
  justify-content: center;
}

/* Input group styles */
.input-group {
  width: 400px;
  display: flex;
  justify-content: center;
  gap: 32px;
}

/* Number input styles */
.number-input {
  width: 100px;
  padding: 8px;
  border: 1px solid #d1d5db;
  border-radius: 4px;
}

/* Error message container styles */
.error-container {
  height: 24px; /* Fixed height for error container */
  margin-top: 16px;
}

/* Error message styles */
.error {
  color: #ef4444;
  text-align: center;
}

/* Slider styles */
input[type="range"] {
  width: 100%;
  height: 8px;
  background: #ddd;
  border-radius: 4px;
  -webkit-appearance: none;
  appearance: none;
  outline: none;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  background: #3b82f6;
  border-radius: 50%;
  cursor: pointer;
}

input[type="range"]::-moz-range-thumb {
  width: 20px;
  height: 20px;
  background: #3b82f6;
  border-radius: 50%;
  cursor: pointer;
  border: none;
}

/* Clock styles */
.clock-container {
  position: fixed;
  bottom: 20px;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
}

.time-display {
  display: flex;
  gap: 16px;
  align-items: center;
  padding: 12px;
  background: #f3f4f6;
  border-radius: 8px;
}

.time-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.time-label {
  display: flex;
  justify-content: space-between;
  gap: 16px;
  font-size: 14px;
  line-height: 1.5;
}

.time-value {
  text-align: right;
  font-family: "Consolas", "Monaco", monospace;
  font-size: 14px;
  letter-spacing: 0.5px;
}
